body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
  /* background-color: #6f8b95; */
  /* background: url('./public/assets/imgs/gradient1.jpg'); */
}

.App-header{
  position: absolute;
  bottom: 10;
  width: 100%;
  height: 10vw;
  border-radius: 0 0 85% 85% / 30%;
}


.Background-img {
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0%;
  

}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#about {
    box-shadow: 10px 0px 22px 0px rgba(0,0,0,0.3);  
    text-align: left; 
    border-radius: 5px;
    margin-top: 4%;
    margin-bottom: 4%;
}

#about .img-wrap {
    padding-left: 0;
}

#about .img-wrap img {
    height: 475px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

#about .bio {
    padding: 2% 5% 0 2%;  
}

#about .connect {
    margin-top: 8%;
    text-align: left;
}

#about .connect img {
    height: 50px;
    width: 50px;
    margin-right: 12px;
}

#about .connect img:hover {
   cursor: pointer;
}

@media only screen and (max-width: 40em) {
    #about {
        margin: 10% 8px;
    }
    #about .img-wrap img {
        height: 350px;
        border-bottom-left-radius: 0px;
    }
    #about .bio {
        padding: 5%;  
    }

} /* max-width 640px, mobile-only styles small */


@media only screen and (min-width: 40.063em) and (max-width: 64em) {

    #about .img-wrap img {
        border-bottom-left-radius: 0px;
    }

} /* min-width 641px and max-width 1024px - tablet styles medium */
#recommendations {
	background: url("/assets/imgs/stone.png") center fixed;
	background-size: cover;
	height: 650px;
	margin: 4% 0;
}

#recommendations .rec {
	margin: 4% 0 0;
}

#recommendations .card {
	height: 450px;
	margin: 20% 1% 0;
	box-shadow: 10px 0px 22px 0px rgba(0,0,0,0.22);  
}

#recommendations .rec1 .card {
	height: 500px;
	margin: 6% 1% 0;
}

#recommendations .card-top {
	height: 25%;
	background-color: rgba(229, 202, 196, .65);
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}

#recommendations .card-btm {
	background-color: green;
	height: 75%;
	background-color: #E5CAC4;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
}

#recommendations .card-btm img {
	height: 150px;
	width: 150px;
	margin-top: -75px;
	margin-bottom: 10px;
}

#recommendations .card-btm p{
	text-align: left;
}

#recommendations .card-btm p span {
	font-family: 'Italianno', cursive;
	font-size: 2em;
	line-height: 0;
}

#recommendations .card-btm .auth {
	text-transform: capitalize;
}

@media only screen and (max-width: 40em) {
	#recommendations {
		height: auto;
		padding-bottom: 5%;
		margin: 10% 0;
	}
	#recommendations .card {
		margin: 10% 1%;
	}

}

@media only screen and (min-width: 40.063em) and (max-width: 64em) {
	#recommendations {
		height: auto !important;
		padding-bottom: 5%;
		margin: 10% 0;
	}
	#recommendations .card {
		margin: 10% 1%;
	}
}




.hero {
    background: url("/assets/imgs/clouds.jpg") center fixed;
    background-size: cover;
    min-height: 95vh;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    margin: 0 -20em 5em;
    position: relative;
    text-align: center;
}
.hero .logo-wrap {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
}

.hero img {
    height: 400px;
    margin-bottom: 3%;
}



@media only screen and (max-width: 40em) {
    .hero img {
        height: 200px ;
        margin-bottom: 1%;
    }
    .hero{
        background: url("/assets/imgs/clouds.jpg")no-repeat top;
        background-size: cover;
        min-height: 500px;
        margin: 0 -14em 5em;
    }
}
@media only screen and (min-width: 40.063em) and (max-width: 64em) {
    .hero img {
        height: 200px ;
      margin-bottom: 1%;
    }
}
footer {
	background-color: #819EA6;
	padding: 2%;
	font-size: 10px;

}
footer p {
	margin: 0;
}
#spacer {
    margin: 4% 0;
    background: url("/assets/imgs/crystal.png") center fixed;
    background-size: cover;
    height: 150px;
}
